<template>
  <el-tabs v-model="activeName">
    <el-tab-pane label="信息" name="first">
      <div class="settings-container">
        <el-card class="box-card">
          <el-row>
            <el-col :span="12">
              <el-form ref="form" :model="user" :rules="rule" label-width="80px"
                       label-position="left">
                <el-form-item label="编号">
                  <el-input :value="user.id" disabled></el-input>
                </el-form-item>
                <el-form-item label="用户名" prop="username">
                  <el-input v-model="user.username"></el-input>
                </el-form-item>
                <el-form-item label="用户昵称" prop="alias">
                  <el-input v-model="user.alias"></el-input>
                </el-form-item>
                <el-form-item label="简介">
                  <el-input type="textarea" v-model="user['bio']"></el-input>
                </el-form-item>
                <el-form-item>
                  <el-button :loading="updateProfileLoading" type="primary" @click="onUpdateUser">保存</el-button>
                </el-form-item>
              </el-form>
            </el-col>
            <el-col class="ss" :span="4" :offset="4">
              <label style="cursor: pointer;" @click="dialogVisible=true">
                <el-image shape="square" :size="150" fit="fill"
                          referrerPolicy="no-referrer"
                          :src="user.avatar">
                </el-image>
              </label>
            </el-col>
          </el-row>
        </el-card>
        <el-dialog
            title="修改头像"
            :visible.sync="dialogVisible"
            append-to-body
        >
          <el-input v-model="updateImage"></el-input>
          <span slot="footer" class="dialog-footer">
        <el-button @click="dialogVisible = false">取 消</el-button>
        <el-button :loading="updatePhotoLoading" type="primary" @click="onUpdatePhoto">确 定</el-button>
      </span>
        </el-dialog>
      </div>
    </el-tab-pane>
    <el-tab-pane label="密码" name="second">
      <div class="settings-container">
        <el-card class="box-card">
          <el-row>
            <el-col :span="12">
              <el-form ref="formPass" :rules="rule" :model="formPassword" label-width="80px"
                       label-position="left">
                <el-form-item label="密码" prop="password">
                  <div style="display: flex">
                    <el-input ref="pass" v-model="formPassword.password" :disabled="!editPassword" type="password"></el-input>
                    <div style="width: 50px;margin-left: 20px" v-show="editPassword===false">
                      <el-link :underline="false" type="warning" @click.stop="editPassword=true">修改</el-link>
                    </div>
                  </div>
                </el-form-item>
                <el-form-item>
                  <el-button  type="primary" @click="updatePassword('formPass')">保存</el-button>
                </el-form-item>
              </el-form>
            </el-col>
          </el-row>
        </el-card>
        <el-dialog
            title="修改头像"
            :visible.sync="dialogVisible"
            append-to-body
        >
          <el-input v-model="updateImage"></el-input>
          <span slot="footer" class="dialog-footer">
        <el-button @click="dialogVisible = false">取 消</el-button>
        <el-button :loading="updatePhotoLoading" type="primary" @click="onUpdatePhoto">确 定</el-button>
      </span>
        </el-dialog>
      </div>
    </el-tab-pane>
  </el-tabs>


</template>

<script>


import {mapState} from "vuex";
import {updateAvatarApi, updateApi, getPasswordApi,updatePasswordApi} from "@/api/user";

export default {
  name: 'SettingsIndex',
  components: {},
  props: {},
  data() {
    return {
      activeName: "first",
      editPassword: false,
      dialogVisible: false, // 控制上传图片裁切预览的显示状态
      updateImage: '',
      cropper: null, // 裁切器示例
      updatePhotoLoading: false, // 更新用户头像的 loading 状态
      updateProfileLoading: false, // 更新基本信息的 loading 状态,
      formPassword: {
        password: "",
      },
      rule: {
        username: [
          {required: true, message: '请输入用户名', trigger: 'blur'},
          {min: 3, max: 10, message: '长度在 1 到 10个字符', trigger: 'blur'}
        ],
        alias: [
          {required: true, message: '请输入用户昵称', trigger: 'blur'},
          {min: 1, max: 10, message: '长度在 1 到 10个字符', trigger: 'blur'}
        ],
        password: [
          {required: true, message: '请输入密码', trigger: 'blur'},
          {min: 1, max: 10, message: '长度在 1 到 10个字符', trigger: 'blur'}
        ],
      }
    }
  },
  computed: {
    ...mapState("user", ['user']),
  },
  watch: {},
  created() {
    this.loadUser()
  },
  mounted() {
    this.updateImage = this.user.avatar
  },
  methods: {
    onUpdateUser() {
      this.updateProfileLoading = true
      let data = this.user
      data.password = null
      updateApi(data).then(() => {
        this.updateProfileLoading = false
      })
    },
    loadUser() {
      getPasswordApi().then(res => {
        this.formPassword.password = res.data
      })
    },
    onUpdatePhoto() {
      // 让确定按钮开始 loading
      this.updatePhotoLoading = true
      updateAvatarApi(this.updateImage).then(() => {
        this.updatePhotoLoading = false
        this.dialogVisible = false
        this.user.avatar = this.updateImage
      })
    },
    updatePassword(formName){
      this.$refs[formName].validate(async (valid) => {
        if (valid) {
          updatePasswordApi(this.formPassword).then(()=>{
            this.$store.dispatch("user/logout")
            this.$router.push("/")
          })
        } else {
          this.$refs.pass.focus()
          return false
        }
      })

    }
  }
}
</script>

<style scoped>
.preview-image-wrap .preview-image {
  display: block;
  max-width: 100%;
  height: 200px;
}
</style>
